<template>
<div>
<div class="title_pic"></div>
<p class="title">
			通关说明:
		</p>
		<div class="instruction">
			本次通关题目数量为10道,答中8道或以上则算通关
		</div>
		<div class="title">
			你的首次通关成绩:
		</div>
		<p class="little">{{list.firstResult}}</p>
		<div class="title">
			你的上次通关成绩:
		</div>
		<p class="little">{{list.lastResult}}</p>
		<button class="comfirm_btn" style="background:#FA8A12 ;margin-top: 0.3rem;" @click="go">开始通关</button>

</div>
</template>

<script>
	
	export default{
	
		data(){
			return {
				      list:{},
				      type:2,
				      id:this.$route.params.ConsoledateId
			}
			
		},
		created(){
			this.getData()
			
		},
		methods:{
			go(){
				this.$router.push({name: 'Test',query:{id:this.id,Action:this.type}})
			},
			getData(){
				var obj={
					"s":this.$store.state.sessionId,
					"a":2,
					"id":this.id
				}
				this.$Api.EveryExercise(obj).then((res)=>{
					console.log(res)
					this.list=res.q.exercise
				})
				
				
			}
		}
	}
</script>

<style scoped="scoped" lang="scss">
@import "../../sass/baseSass.scss";
.title_pic{
	width: 100%;
	height: 2.4rem;
	background: url(../../assets/clear.png) no-repeat;
	background-size:100% 100%;
}
.title_pic{
	width: 100%;
	height: 2.4rem;
	background: url(../../assets/test.png) no-repeat;
	background-size:100% 100%;
}
.title{
	padding: 0.3rem 0.3rem 0 0.3rem;
	font-size: 0.32rem;
}
.instruction{
	padding: 0.3rem;
	font-size: 0.3rem;
}
.little{
	color: #fa8a12;
	font-size: 0.28rem;
		padding: 0.2rem 0.3rem 0 0.3rem;
	
}

</style>


